import React, { Component } from 'react';
import { Col, Card, Row, Table, Select, Form, Popconfirm, message } from 'antd';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import {
  getPaperStudent,
  getPapeType,
  savePaperType,
  deletePaper,
  getUser,
} from 'services/AdditionalAdmissionPapersPage';
import { BtnDelete, BtnSave } from 'components/common/button';
import { dateToString } from '../../../utils/formatUtil';

const { Option } = Select;
@Form.create({ name: 'FormSearchStudent' })
class PaperStudentDetail extends Component {
  state = {
    paperType: [],
    paperStudent: [],
    paperId: '',
    loading: false,
    token: '',
    studentId: '',
  };

  componentDidMount = () => {
    const { match } = this.props;
    this.getDataPaperStudent(match.params.id);
    getUser()
      .then(res => {
        this.setState({
          token: res.data.accessToken,
        });
      })
      .catch(error => {
        console.log(error);
      });
    getPapeType()
      .then(res => {
        this.setState({
          paperType: res.data.items,
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  getDataPaperStudent = id => {
    this.setState({
      loading: true,
    });
    getPaperStudent(id)
      .then(res => {
        this.setState({
          paperStudent: res.data.items,
          studentId: id,
          loading: false,
        });
      })
      .catch(error => {
        message.error(error.response.data.message);
        this.setState({
          loading: false,
        });
      });
  };

  changePaperType = value => {
    this.setState({
      paperId: value,
    });
  };

  deletePaper = recorđ => {
    const { studentId } = this.state;
    deletePaper(studentId, recorđ.paperTypeId)
      .then(() => {
        this.getDataPaperStudent(studentId);
        message.success('Xoá thành công');
      })
      .catch(error => {
        console.log(error);
      });
  };

  savePaperType = () => {
    const { studentId, paperId, token } = this.state;
    savePaperType(studentId, paperId, token)
      .then(() => {
        message.success('Thêm giấy tờ thành công');
        this.getDataPaperStudent(studentId);
        this.props.form.resetFields();
        this.setState({
          paperId: '',
        });
      })
      .catch(error => {
        message.error(error.response.data.message);
      });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { paperType, paperStudent, paperId, loading } = this.state;
    const columns = [
      {
        title: 'Mã giấy tờ',
        dataIndex: 'paperTypeId',
        key: 'paperTypeId',
      },
      {
        title: 'Tên giấy tờ',
        dataIndex: 'paperTypeLabel',
        key: 'paperTypeLabel',
      },
      {
        title: 'Ngày nhận',
        dataIndex: 'receivedDate',
        key: 'receivedDate',
        render: (text, record) => <div>{dateToString(record.receivedDate)}</div>,
      },
      {
        title: 'Thao tác',
        render: (text, record) => (
          <Popconfirm
            title="Đồng ý xóa ?"
            onConfirm={() => this.deletePaper(record)}
            okText="Xóa"
            cancelText="Hủy"
          >
            <BtnDelete />
          </Popconfirm>
        ),
      },
    ];
    return (
      <Card title="Thông tin giấy tờ">
        <Row>
          <Col span={24}>
            <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
              <Row gutter={24}>
                <Col span={12}>
                  <Form.Item label="Loại giấy tờ">
                    {getFieldDecorator('fullName', {})(
                      <Select style={{ width: '100%' }} onChange={this.changePaperType}>
                        {paperType.map(item => (
                          <Option key={item.id} value={item.id}>
                            {item.label}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </Row>
            </Form>
          </Col>
          <Col span={24}>
            <Table loading={loading} columns={columns} dataSource={paperStudent} rowKey="id" />
          </Col>
          <Col offset={8} span={12}>
            {paperId ? <BtnSave onClick={this.savePaperType} htmlType="submit" /> : null}
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <PaperStudentDetail {...props} />
  </Page>
));



